<style lang="less">
.task-detail {
  .progress-box .el-form-item__label {
    width: 200px !important;
  }
  .radiu-box {
    width: 100%;
    border: 1px solid #dcdfe6;
    min-height: 140px;
    border-radius: 5px;
    padding: 24px;
  }
  .radiu-box p img {
    width: 25px;
    display: inline-block;
  }
  .progressList {
    margin-bottom: 20px;
  }
  .progressList p {
    font-size: 14px;
    padding-left: 10px;
    margin-bottom: 10px;
  }
  .down-btn em {
    max-width: 157px;
    display: inline-block;
    height: 40px;
    line-height: 40px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    vertical-align: middle;
  }
  .down-btn video {
    height: 200px;
  }
  .down-btn span video {
    height: 200px;
  }
  .down-btn a {
    display: inline-block;
  }
  .down-btn img {
    display: inline-block;
    width: 20px;
    margin-right: 7px;
    vertical-align: middle;
    border-radius: 3px;
  }
  .down-btn .el-button {
    font-size: 12px;
    padding: 5px 10px;
    margin-left: 20px;
  }
  .downIcon {
    font-size: 12px;
    padding: 5px 10px;
    margin-left: 20px;
    color: #409eff;
    background: #ecf5ff;
    border: 1px solid #d8dce5;
    display: inline-block;
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    -webkit-appearance: none;
    text-align: center;
    transition: 0.1s;
    border-radius: 4px;
  }
  .el-form-item__label {
    color: #99a9bf;
  }
  .quote-title {
    margin-bottom: 20px;
    padding: 12px 15px;
    line-height: 22px;
    border-left: solid 5px #00a2ca;
    border-radius: 0 2px 2px 0;
    background-color: #f2f2f2;
  }
  .wrap-detais-content {
    position: relative;
    margin: 50px;
    padding: 15px;
    border: 1px solid #ccc;
  }
  .detail-title-h3 {
    position: absolute;
    width: 100px;
    text-align: center;
    height: 40px;
    line-height: 40px;
    top: -20px;
    left: 20px;
    background: #fff;
  }
  .operateBox {
    text-align: center;
    float: none;
  }
  .box-card-bot {
    margin-bottom: 30px;
  }
  .ele-textarea-width {
    width: 500px;
  }
  .ele-evaluate-btn {
    display: block;
    margin: 20px auto;
  }
  .successTxt {
    color: #67c23a;
  }
  #editable {
    min-width: 500px;
    min-height: 200px;
    display: block;
  }
  .el-textarea__inner {
    min-height: 130px;
  }
  .leftText label {
    text-align: left;
  }
  .el-textarea.is-disabled .el-textarea__inner {
    background: none;
    color: #323335;
  }
}
</style>

